import { toValue } from "../../../util/currency-formatter";

const validator = {
  value: {
    rules: [
      {
        test: /^R\$ [1-9][0-9]+(,[0-9]+)?$/,
        message: "Valor invlido."
      },
      {
        test: e => {
          const res = Number(toValue(e));
          return res < 10000;
        },
        message:
          "Valor mximo para recarga  R$ 10.000,00, se voc precisar de um valor maior, entre em contato com a gente"
      }
    ],
    errors: [],
    valid: false,
    state: "",
    mask: ""
  },
  value2: {
    rules: [
      {
        test: /^R\$ [1-9][0-9]+(,[0-9]+)?$/,
        message: "Valor invlido."
      },
      {
        test: e => {
          const res = Number(toValue(e));
          return res < 10000;
        },
        message:
          "Valor mximo para recarga  R$ 10.000,00, se voc precisar de um valor maior, entre em contato com a gente"
      }
    ],
    errors: [],
    valid: false,
    state: "",
    mask: ""
  },
  date: {
    rules: [
      {
        test: e => e < 31 && e > 0,
        message: "Dia selecionado invlido."
      }
    ],
    errors: [],
    valid: false,
    state: "",
    mask: "99"
  },
  date2: {
    rules: [
      {
        test: e => e < 31 && e > 0,
        message: "Dia selecionado invlido."
      }
    ],
    errors: [],
    valid: false,
    state: "",
    mask: "99"
  },
  dropdownRate: {
    rules: [
      {
        test: e => e > 1 || e < 0,
        message: "Dia selecionado invlido."
      }
    ],
    errors: [],
    valid: false,
    state: "",
    mask: "99"
  }
};

export default validator;



