import React from "react";
import { Row, Col } from "reactstrap";
import { GenerateRecharge } from "./generate-recharge/generate-recharge";
import { AutoRecharge } from "./auto-recharge/auto-recharge";

class Recharge extends React.Component {
  state = {
    user: {
      nCard: "5111 1111 1111 1111",
      valCard: "01/2020"
    }
  };

  render() {
    const { user } = this.state;
    return (
      <Row>
        <Col className="widget">
          <AutoRecharge />
        </Col>
        <Col className="widget">
          <GenerateRecharge user={user} />
        </Col>
      </Row>
    );
  }
}

export default Recharge;



