import Home from "../views/home/home";
import Statement from "../views/statement/statement";
import Block from "../views/block/block";
import Change from "../views/change/change";
import Recharge from "../views/recharge/recharge";
import { CardActivation } from "../views/card-activation/card-activation";
import { TalkToUs } from "../views/talk-to-us/talk-to-us";
import MyAccount from "../views/my-account/my-account";

const ThemeRoutes = [
  {
    path: "/home",
    name: "Home",
    icon: "ti-home",
    component: Home
  },
  {
    path: "/statement",
    name: "Extrato",
    icon: "ti-layout-list-thumb",
    component: Statement
  },
  {
    path: "/recarregar",
    name: "Recarregar Carto",
    icon: "ti-reload",
    component: Recharge
  },
  {
    path: "/bloquear",
    name: "Bloquear Carto",
    icon: "ti-credit-card",
    component: Block
  },
  {
    path: "/alterar-senha",
    name: "Alterar senha",
    icon: "ti-key",
    component: Change
  },

  {
    path: "/fale-conosco",
    name: "Fale Conosco",
    icon: "ti-comment",
    component: TalkToUs
  },
  {
    path: "/ativar-cartao",
    notInSidebar: true,
    component: CardActivation
  },
  {
    path: "/minha-conta",
    notInSidebar: true,
    component: MyAccount
  },
  { path: "/", pathTo: "/home", name: "Home", redirect: true }
];
export default ThemeRoutes;



