import React, { Component } from "react";
import { Row, Col, Card, CardBody, CardTitle, Button, Form } from "reactstrap";
import Validator from "./validator";
import InputBanky from "../../../components/input-banky/input-banky";
import { toCurrency } from "../../../util/currency-formatter";

export class GenerateRecharge extends Component {
  state = {
    value: ""
  };

  constructor(props) {
    super(props);
    this.state.user = props.user;
    this.validators = Validator;
  }

  handleComponentChanges = input => e => {
    this.setState({ [input]: toCurrency(e.target.value) });
  };

  handleFormClick = () => {
    if (this.validForm()) console.log("boleto gerado");
  };

  validForm = () => {
    let status = true;
    Object.keys(this.validators).forEach(field => {
      const { [field]: currentState } = this.state;
      this.formValidators(field, currentState);
      if (!this.validators[field].valid) {
        status = false;
      }
    });
    return status;
  };

  formValidators(fieldName, value) {
    this.validators[fieldName].errors = [];
    this.validators[fieldName].state = value;
    this.validators[fieldName].valid = true;
    this.validators[fieldName].rules.forEach(rule => {
      if (rule.test instanceof RegExp) {
        if (!rule.test.test(value)) {
          this.validators[fieldName].errors.push(rule.message);
          this.validators[fieldName].valid = false;
        }
      } else if (typeof rule.test === "function") {
        if (!rule.test(value)) {
          this.validators[fieldName].errors.push(rule.message);
          this.validators[fieldName].valid = false;
        }
      }
    });
    this.setState(st => st);
  }

  showErrors(fieldName) {
    if (fieldName === "") {
      return <div className="error" />;
    }
    const validator = this.validators[fieldName];

    if (validator && !validator.valid) {
      const errors = validator.errors.map((info, index) => (
        // eslint-disable-next-line react/no-array-index-key
        <span className="error" key={index}>
          <i className="mdi mdi-alert-octagram" /> {info}
        </span>
      ));
      return <div className="error">{errors}</div>;
    }
    return <div className="error" />;
  }

  render() {
    const { value } = this.state;
    return (
      <Card className="generateRechargeComponent">
        <CardBody>
          <Row className="justify-content-around">
            {/* Ttulo */}
            <Col xs="12">
              <CardTitle className="section-card-title">
                <i className="ti-credit-card" /> Recarregar carto
              </CardTitle>
            </Col>

            <Col xs="12">
               s realizar o pagamento do boleto, pode deixar que a gente cuida
              do resto.
            </Col>
            <Col xs="12">
              <Form className="form-material mt-3">
                <InputBanky
                  label="Digite o valor da recarga"
                  type="text"
                  onChange={this.handleComponentChanges("value")}
                  value={value}
                  name="value"
                  mask={this.validators.value.mask}
                  formNoValidate
                  required
                />
                {this.showErrors("value")}
              </Form>
            </Col>
            <Col xs="12" className="text-center mt-3">
              <Button
                type="button"
                onClick={this.handleFormClick}
                className="btn btn-info"
              >
                Gerar Boleto
              </Button>
            </Col>
            <Col xs="12" className="text-muted mt-3">
              *Aps efetuar o pagamento, o saldo ser debitado em D+1.
            </Col>
          </Row>
        </CardBody>
      </Card>
    );
  }
}

export default GenerateRecharge;



