import React from "react";
import { Row, Col, Card, CardBody, CardTitle, Button } from "reactstrap";
import { CardComponent } from "../../components/card-component/card-component";

// eslint-disable-next-line react/prefer-stateless-function
class Block extends React.Component {
  render() {
    const user = {
      nCard: "5111 1111 1111 1111",
      valCard: "01/2020"
    };
    return (
      <Row className="blockComponent">
        <Col className="widget">
          <Card>
            <CardBody>
              <Row>
                {/* Ttulo */}
                <Col xs="12">
                  <CardTitle className="section-card-title">
                    <i className="ti-lock" /> Bloquear carto
                  </CardTitle>
                </Col>

                {/* Bloquear */}
                <Col xs="12" md="6" className="text-center  mb-3">
                  <b>
                    Voc perdeu seu carto ou ele foi furtado? Bloqueie aqui
                  </b>

                  <div className="text-muted my-3 text-center">
                    Bloquear seu carto nunca foi to fcil
                  </div>
                  <Button className="btn btn-info">Bloquear Carto</Button>
                </Col>

                {/* Imagem carto */}
                <Col xs="12" md="6" className="text-center mb-3">
                  <CardComponent user={user} />
                </Col>
              </Row>
            </CardBody>
          </Card>
        </Col>

        <Col className="widget">
          {/* Aviso */}
          <Card className="p-3">
            <CardBody>
              <CardTitle className="mb-0 text-left pb-1">
                Desbloquear carto
              </CardTitle>
              <div>
                <div className="text-muted  text-left my-3">
                  Caso voc recupere seu carto perdido,  s mandar uma
                  mensagem para ns via whatsapp (11) 9999 99999 ou e-mail
                  resolvemos@banky.com.br que iremos te atender o mais rpido
                  possvel.
                </div>
              </div>
            </CardBody>
          </Card>
        </Col>
      </Row>
    );
  }
}

export default Block;



