import React from "react";
import {
  Row,
  Col,
  Card,
  CardBody,
  CardTitle,
  Button,
  Form,
  FormGroup,
  Input
} from "reactstrap";
import { CardComponent } from "../../components/card-component/card-component";

class Change extends React.Component {
  state = {
    user: {
      nCard: "5111 1111 1111 1111",
      valCard: "01/2020"
    }
  };

  render() {
    const { user } = this.state;
    return (
      <Row>
        <Col className="widget">
          <Card className="changeComponent">
            <CardBody>
              <Row className="justify-content-around">
                {/* Ttulo */}
                <Col xs="12">
                  <CardTitle className="section-card-title">
                    <i className="ti-key" /> Deseja trocar a senha do carto?
                  </CardTitle>
                </Col>

                {/* Bloquear */}
                <Col xs="12" md="6" className="text-center mb-3">
                  <b>Cansou da senha atual?</b>

                  <div className="text-muted my-3 text-justify">
                    Digite abaixo sua nova senha do carto
                  </div>
                  <Form>
                    <FormGroup>
                      <Input
                        type="password"
                        placeholder="Digite a senha antiga"
                        required
                      />
                    </FormGroup>
                    <FormGroup>
                      <Input
                        type="password"
                        placeholder="Digite a senha nova"
                        required
                      />
                    </FormGroup>
                    <FormGroup>
                      <Input
                        type="password"
                        placeholder="Confirme a senha nova"
                        required
                      />
                    </FormGroup>
                    <Button type="submit" className="btn btn-info">
                      Trocar Senha
                    </Button>
                  </Form>
                </Col>
                <Col xs="12" md="6" className="my-auto">
                  {/* Imagem carto */}
                  <CardComponent user={user} />
                </Col>
              </Row>
            </CardBody>
          </Card>
        </Col>
      </Row>
    );
  }
}

export default Change;



