import React, { Component } from "react";
import {
  Row,
  Col,
  Card,
  CardBody,
  CardTitle,
  Form,
  FormGroup,
  Label,
  Input
} from "reactstrap";

export class MyAccount extends Component {
  state = {};

  render() {
    return (
      <Row>
        <Col className="widget-full">
          <Card className="changeComponent">
            <CardBody>
              <Row className="justify-content-around">
                {/* Ttulo */}
                <Col xs="12" className="mb-3">
                  <CardTitle className="section-card-title">
                    <i className="ti-user" /> Minha Conta
                  </CardTitle>
                </Col>
              </Row>

              <Col xs="12">
                <Form className="form-material">
                  <Row>
                    <Col xs="12" className="mb-3">
                      <b>Informaes cadastrais</b>
                    </Col>
                    <Col xs="12" md="6">
                      <FormGroup>
                        <Label>CNPJ</Label>
                        <Input
                          type="text"
                          placeholder="70.011.024 / 0001 - 22"
                          disabled
                        />
                      </FormGroup>
                    </Col>
                    <Col xs="12" md="6">
                      <FormGroup>
                        <Label>Empresa</Label>
                        <Input
                          type="text"
                          placeholder="Coca Cola - Brasil"
                          disabled
                        />
                      </FormGroup>
                    </Col>
                    <Col xs="12" md="6">
                      <FormGroup>
                        <Label>Responsvel</Label>
                        <Input
                          type="text"
                          placeholder="Fernando dos Santos"
                          disabled
                        />
                      </FormGroup>
                    </Col>
                    <Col xs="6" md="3">
                      <FormGroup>
                        <Label>CPF</Label>
                        <Input
                          type="text"
                          placeholder="123.456.789 - 10"
                          disabled
                        />
                      </FormGroup>
                    </Col>
                    <Col xs="6" md="3">
                      <FormGroup>
                        <Label>Celular</Label>
                        <Input
                          type="text"
                          placeholder="( 11 ) 99457 - 5112"
                          disabled
                        />
                      </FormGroup>
                    </Col>
                    <Col xs="12" className="my-3">
                      <b>Endereo</b>
                    </Col>
                    <Col xs="12">
                      <FormGroup>
                        <Label>Celular</Label>
                        <Input
                          type="text"
                          placeholder="Av. Paulista, 2200"
                          disabled
                        />
                      </FormGroup>
                    </Col>
                    <Col xs="12" md="6">
                      <FormGroup>
                        <Label>CEP</Label>
                        <Input type="text" placeholder="12345-123" disabled />
                      </FormGroup>
                    </Col>
                    <Col xs="12" md="6">
                      <FormGroup>
                        <Label>Bairro</Label>
                        <Input type="text" placeholder="Centro" disabled />
                      </FormGroup>
                    </Col>
                    <Col xs="12" md="6">
                      <FormGroup>
                        <Label>Cidade</Label>
                        <Input type="text" placeholder="So Paulo" disabled />
                      </FormGroup>
                    </Col>
                    <Col xs="12" md="6">
                      <FormGroup>
                        <Label>Estado</Label>
                        <Input type="text" placeholder="So Paulo" disabled />
                      </FormGroup>
                    </Col>
                  </Row>
                </Form>
              </Col>
            </CardBody>
          </Card>
        </Col>
      </Row>
    );
  }
}

export default MyAccount;



