import React from "react";
import { Row, Col, Card, CardBody, CardTitle } from "reactstrap";

export const TalkToUs = () => (
  <Row id="talkToUsComponent">
    <Col className="widget">
      <Card className="changeComponent">
        <CardBody>
          <Row className="justify-content-around">
            {/* Ttulo */}
            <Col xs="12">
              <CardTitle className="section-card-title">
                <i className="ti-comment" /> Fale conosco
              </CardTitle>
            </Col>
            <Col xs="12" className="mb-3">
              <b>Est com algum problema?</b>
            </Col>
            <Col xs="12" className="mb-3">
              No se preocupe, ns iremos resolver o seu problema,  s enviar
              uma mensagem no nosso whatsapp ou e-mail{" "}
              <b>resolvemos@banky.com.br</b> que iremos te ajudar.
            </Col>
            <Col xs="6" className="icon text-center ">
              <a href="https://wa.me/5511945071807?text=Ol%C3%A1!%20Eu%20preciso%20de%20ajuda.">
                <i className="fab fa-whatsapp wpp" />
              </a>
            </Col>
            <Col xs="6" className="icon text-center">
              <a href="mailto:resolvemos@banky.com.br?Subject=Ol%C3%A1%21%20Eu%20preciso%20de%20ajuda.">
                <i className="mdi mdi-email" />
              </a>
            </Col>
          </Row>
        </CardBody>
      </Card>
    </Col>
  </Row>
);

export default TalkToUs;



