import { toValue } from "../../../util/currency-formatter";

const validator = {
  value: {
    rules: [
      {
        test: /^R\$ [1-9][0-9]+(,[0-9]+)?$/,
        message: "Valor invlido."
      },
      {
        test: e => {
          const res = toValue(e);
          return res > 10000;
        },
        message:
          "Valor mximo para recarga  R$ 10.000,00, se voc precisar de um valor maior, entre em contato com a gente"
      }
    ],
    errors: [],
    valid: false,
    state: "",
    mask: ""
  }
};

export default validator;



