import React from "react";
import { Row, Col } from "reactstrap";
import Balance from "./your-card/your-card";

const Home = props => {
  const { user, history, cardStatus } = props;
  return (
    <Row id="homeComponent">
      <Col className="widget">
        <Balance cardStatus={cardStatus} user={user} history={history} />
      </Col>
    </Row>
  );
};

export default Home;



