import React, { Component } from "react";
import { Button, Row, Col, Container } from "reactstrap";

export class StepZero extends Component {
  // Testa a validade das informaes e avana para prxima etapa
  continue = e => {
    const { nextStep } = this.props;
    e.preventDefault();
    nextStep();
  };

  render() {
    return (
      <Container fluid className="self-registerIntroduction h-100">
        <Row className="justify-content-center">
          <Col xs="12" className="text-center ">
            <h1 className="title">Voc foi selecionado!</h1>
          </Col>
        </Row>
        <Row className="justify-content-center">
          <Col xs="12" className="description text-center">
            Agora basta completar o cadastro, que voc receber seu carto
            corporativo Banky em at 7 dias teis.
          </Col>
        </Row>
        <Row className="justify-content-center">
          <Col className="col-12 col-sm-auto mx-auto">
            <Button
              size="lg"
              block
              type="submit"
              onClick={this.continue}
              className="btn btn-outline-light"
            >
              Continuar
            </Button>
          </Col>
        </Row>
      </Container>
    );
  }
}

export default StepZero;



