import React from "react";
import { Button, Row, Col, Card, CardBody, CardTitle } from "reactstrap";
import CardComp from "../../../components/card-component/card-component";

class YourCard extends React.Component {
  state = {
    user: {
      nCard: "",
      valCard: ""
    }
  };

  handleComponentChanges = input => e => {
    const { user } = this.state;
    user[input] = e.target.value;
    this.setState({ user });
  };

  activeCard = () => {
    const { history } = this.props;
    history.push("/ativar-cartao");
  };

  cardIsInactive = () => (
    <Col xs="12" md="6" className="text-center">
      <Row className="align-items-center mb-3">
        <Col xs="12" className="pb-3">
          <b>Voc recebeu seu carto?</b>
        </Col>
        <Col xs="12" className="mb-2 text-justify">
          Para desbloquear seu carto  simples. Basta clicar no boto abaixo,
          preencher as informaes solicitados e pronto. Seu carto est pronto
          para ser utilizado.
        </Col>
        <Col xs="12" className="mt-2 text-center">
          <Button color="primary" onClick={this.activeCard}>
            Quero ativar meu carto
          </Button>
        </Col>
      </Row>
    </Col>
  );

  cardIsActive = () => (
    <Col xs="12" md="6" className="text-center">
      <Card className="balance-card">
        <CardBody>
          <Row className="align-items-center">
            <Col xs="12" className="mb-2">
              <span>Saldo atualizado as 10:45</span>
            </Col>
            <Col xs="12" className="my-2">
              <span className="display-6 text-success my-auto">R$ 35,28</span>
            </Col>
            <Col xs="12" className="mt-2">
              <span className="text-muted">Gasto no ms: R$ 300,45</span>
            </Col>
          </Row>
        </CardBody>
      </Card>
    </Col>
  );

  render() {
    const { cardStatus, user } = this.props;
    return (
      <Card id="yourCardComponent">
        <CardBody>
          <Row>
            {/* Title */}
            <Col xs="12">
              <CardTitle className="section-card-title">
                <i className="icon-wallet" /> Seu carto
              </CardTitle>
            </Col>

            {cardStatus ? this.cardIsActive() : this.cardIsInactive()}

            {/* Card Picture */}
            <Col xs="12" md="6" className=" ">
              <CardComp user={user} />
            </Col>
          </Row>
        </CardBody>
      </Card>
    );
  }
}

export default YourCard;



