import React, { Component } from "react";
import {
  Row,
  Col,
  Card,
  CardBody,
  CardTitle,
  Button,
  Form,
  FormGroup,
  Input
} from "reactstrap";
import Validator from "./validator";
import InputBanky from "../../../components/input-banky/input-banky";
import { toCurrency } from "../../../util/currency-formatter";

export class AutoRecharge extends Component {
  state = {
    dropdownRate: "quinzenal",
    mensal: {
      value: "",
      date: ""
    },
    quinzenal: {
      value: "",
      value2: "",
      date: "",
      date2: ""
    }
  };

  constructor(props) {
    super(props);
    this.validators = Validator;
  }

  handleComponentChanges = input => e => {
    const { dropdownRate } = this.state;
    const { [dropdownRate]: formFields } = this.state;
    if (input === "dropdownRate") {
      this.setState({ dropdownRate: e.target.value });
    } else if (input === "value" || input === "value2") {
      formFields[input] = toCurrency(e.target.value);
      this.setState({ [dropdownRate]: formFields });
    } else {
      formFields[input] = e.target.value;
      this.setState({ [dropdownRate]: formFields });
    }
  };

  handleFormClick = () => {
    if (this.validForm()) console.log("Auto boleto cadastrado.");
  };

  validForm = () => {
    let status = true;
    const { dropdownRate } = this.state;
    const { [dropdownRate]: formFields } = this.state;
    Object.keys(formFields).forEach(field => {
      const { [field]: currentState } = formFields;
      this.formValidators(field, currentState);
      if (!this.validators[field].valid) {
        status = false;
      }
    });
    return status;
  };

  formValidators(fieldName, value) {
    this.validators[fieldName].errors = [];
    this.validators[fieldName].state = value;
    this.validators[fieldName].valid = true;
    this.validators[fieldName].rules.forEach(rule => {
      if (rule.test instanceof RegExp) {
        if (!rule.test.test(value)) {
          this.validators[fieldName].errors.push(rule.message);
          this.validators[fieldName].valid = false;
        }
      } else if (typeof rule.test === "function") {
        if (!rule.test(value)) {
          this.validators[fieldName].errors.push(rule.message);
          this.validators[fieldName].valid = false;
        }
      }
    });
    this.setState(st => st);
  }

  showErrors(fieldName) {
    if (fieldName === "") {
      return <div className="error" />;
    }
    const validator = this.validators[fieldName];

    if (validator && !validator.valid) {
      const errors = validator.errors.map((info, index) => (
        // eslint-disable-next-line react/no-array-index-key
        <span className="error" key={index}>
          <i className="mdi mdi-alert-octagram" /> {info}
        </span>
      ));
      return <div className="error">{errors}</div>;
    }
    return <div className="error" />;
  }

  mensalForm() {
    const {
      mensal: { value, date }
    } = this.state;
    return (
      <Row>
        <Col xs="12">
          <b>Dados do boleto</b>
        </Col>

        <Col xs="6">
          <InputBanky
            label="Digite o valor da recarga"
            type="text"
            onChange={this.handleComponentChanges("value")}
            value={value}
            name="value"
            mask={this.validators.value.mask}
            formNoValidate
            required
          />
          {this.showErrors("value")}
        </Col>
        <Col xs="6">
          <InputBanky
            label="Dia"
            type="text"
            onChange={this.handleComponentChanges("date")}
            value={date}
            name="date"
            mask={this.validators.date.mask}
            formNoValidate
            required
          />
          {this.showErrors("date")}
        </Col>
      </Row>
    );
  }

  quinzenalForm() {
    const {
      quinzenal: { value, value2, date, date2 }
    } = this.state;
    return (
      <Row className="justify-content-center  no-gutters">
        <Col xs="12">
          <b>Primeiro Boleto</b>
        </Col>
        <Col xs="8">
          <InputBanky
            label="Valor"
            type="text"
            onChange={this.handleComponentChanges("value")}
            value={value}
            name="value"
            mask={this.validators.value.mask}
            formNoValidate
            required
          />
          {this.showErrors("value")}
        </Col>
        <Col xs="4">
          <InputBanky
            label="Dia"
            type="text"
            onChange={this.handleComponentChanges("date")}
            value={date}
            name="date"
            mask={this.validators.date.mask}
            formNoValidate
            required
          />
          {this.showErrors("date")}
        </Col>
        <Col xs="12" className="mt-3">
          <b>Segundo Boleto</b>
        </Col>
        <Col xs="8">
          <InputBanky
            label="Valor"
            type="text"
            onChange={this.handleComponentChanges("value2")}
            value={value2}
            name="value2"
            mask={this.validators.value2.mask}
            formNoValidate
            required
          />
          {this.showErrors("value")}
        </Col>
        <Col xs="4">
          <InputBanky
            label="Vencimento"
            type="text"
            onChange={this.handleComponentChanges("date2")}
            value={date2}
            name="date2"
            mask={this.validators.date2.mask}
            formNoValidate
            required
          />
          {this.showErrors("date2")}
        </Col>
      </Row>
    );
  }

  render() {
    const { dropdownRate } = this.state;
    return (
      <Card className="autoRechargeComponent">
        <CardBody>
          <Row className="justify-content-around">
            {/* Ttulo */}
            <Col xs="12">
              <CardTitle className="section-card-title">
                <i className="ti-back-right" /> Recarga Automtica
              </CardTitle>
            </Col>
            <Col xs="12">
              Criamos esse servio para que voc no tenha que se preocupar toda
              vez em solicitar o boleto para recarga. Seus boletos esto
              programados para serem enviados periodicamente, 7 dias antes do
              vencimento.
            </Col>
            <Col xs="12">
              <Form className="form-material mt-3">
                <Row>
                  <Col xs="12">
                    <b>Perodo</b>
                  </Col>
                  <Col xs="12">
                    <FormGroup className="nonBankyComponent">
                      <Input
                        type="select"
                        onChange={this.handleComponentChanges("dropdownRate")}
                        value={dropdownRate}
                      >
                        <option value="quinzenal">Quinzenal</option>
                        <option value="mensal">Mensal</option>
                      </Input>
                    </FormGroup>
                  </Col>
                  <Col xs="12">
                    {dropdownRate === "mensal"
                      ? this.mensalForm()
                      : this.quinzenalForm()}
                  </Col>
                </Row>
              </Form>
            </Col>
            <Col xs="12" className="text-center mt-3">
              <Button
                type="button"
                onClick={this.handleFormClick}
                className="btn btn-info"
              >
                Salvar envio
              </Button>
            </Col>
          </Row>
        </CardBody>
      </Card>
    );
  }
}

export default AutoRecharge;



