import React, { Component } from "react";
import { Button, Row, Col, Card, CardBody, CardTitle, Form } from "reactstrap";
import Validator from "./validator";
import InputBanky from "../../components/input-banky/input-banky";
import { CardComponent } from "../../components/card-component/card-component";

export class CardActivation extends Component {
  state = {
    user: {
      nCard: "",
      valCard: ""
    }
  };

  constructor(props) {
    const { user } = props;
    super(props);
    this.validators = Validator;
    this.user = user;
    console.log(props);
  }

  handleComponentChanges = input => e => {
    const { handleComponentChanges } = this.props;
    const { user } = this.state;
    user[input] = e.target.value;
    this.setState({ user });
    handleComponentChanges(input)(e);
  };

  cardIsInactive = () => {
    const { step } = this.state;
    switch (step) {
      case 0:
        return this.cardForm();
      case 1:
        return this.cardDone();
      default:
        return this.cardForm();
    }
  };

  cardForm = () => {
    const {
      user: { nCard, valCard }
    } = this.state;
    return (
      <Row>
        <Col xs="12" className="mb-2 text-justify">
          Digite as informaes abaixo para realizar a ativao do seu carto
          Banky
        </Col>
        <Col xs="12" className="">
          {/*      */}
          {/* Form */}
          {/*      */}
          <Form className="form-material mb-auto" autoComplete="off">
            <Row className="justify-content-center align-content-top">
              <Col xs="12" className="mx-auto">
                <InputBanky
                  label="Nmero do Carto"
                  type="text"
                  onChange={this.handleComponentChanges("nCard")}
                  value={nCard}
                  name="nCard"
                  mask={this.validators.nCard.mask}
                  formNoValidate
                  required
                />
                {this.showErrors("nCard")}
              </Col>
            </Row>
            <Row className="justify-content-center align-content-top">
              <Col xs="12" className="mx-auto">
                <InputBanky
                  label="Validade (MM/AAAA)"
                  type="text"
                  onChange={this.handleComponentChanges("valCard")}
                  value={valCard}
                  name="valCard"
                  mask={this.validators.valCard.mask}
                  formNoValidate
                  required
                />
                {this.showErrors("valCard")}
              </Col>
            </Row>
          </Form>
        </Col>
        <Col xs="12" className="mt-2">
          <Button color="primary" onClick={this.validForm}>
            Quero ativar meu carto
          </Button>
        </Col>
      </Row>
    );
  };

  validForm = () => {
    let status = true;
    const { changeCardStatus } = this.props;
    Object.keys(this.validators).forEach(field => {
      const {
        user: { [field]: currentState }
      } = this.state;
      this.formValidators(field, currentState);
      if (!this.validators[field].valid) {
        status = false;
      }
    });
    if (status) changeCardStatus(status);
  };

  formValidators(fieldName, value) {
    this.validators[fieldName].errors = [];
    this.validators[fieldName].state = value;
    this.validators[fieldName].valid = true;
    this.validators[fieldName].rules.forEach(rule => {
      if (rule.test instanceof RegExp) {
        if (!rule.test.test(value)) {
          this.validators[fieldName].errors.push(rule.message);
          this.validators[fieldName].valid = false;
        }
      } else if (typeof rule.test === "function") {
        if (!rule.test(value)) {
          this.validators[fieldName].errors.push(rule.message);
          this.validators[fieldName].valid = false;
        }
      }
    });
    this.setState(st => st);
  }

  showErrors(fieldName) {
    if (fieldName === "") {
      return <div className="error" />;
    }
    const validator = this.validators[fieldName];

    if (validator && !validator.valid) {
      const errors = validator.errors.map((info, index) => (
        // eslint-disable-next-line react/no-array-index-key
        <span className="error" key={index}>
          <i className="mdi mdi-alert-octagram" /> {info}
        </span>
      ));
      return <div className="error">{errors}</div>;
    }
    return <div className="error" />;
  }

  render() {
    const { cardStatus, history, user } = this.props;

    // Se o carto j est ativo
    if (cardStatus) {
      history.push("/home");
    }
    return (
      <Row>
        <Col className="widget">
          <Card className="activationCardComponent">
            <CardBody>
              <Row className="justify-content-around">
                {/* Ttulo */}
                <Col xs="12">
                  <CardTitle className="section-card-title">
                    <i className="ti-credit-card" /> Ativar carto
                  </CardTitle>
                </Col>
                <Col xs="12" md="6">
                  {this.cardForm()}
                </Col>
                <Col xs="12" md="6">
                  <CardComponent user={user} />
                </Col>
              </Row>
            </CardBody>
          </Card>
        </Col>
      </Row>
    );
  }
}

export default CardActivation;



